@use "./_utils" as u

#curation-wrapper
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-start

    .curation-search
        background-color: white
        margin: 10px
        width: 80%

    .curation-display
        margin: 10px
        width: 80%
        display: flex
        flex-wrap: wrap
        justify-content: space-between

        .curation-image-container
            width: 24%
            margin-bottom: 10px
            height: 0%
            background-color: white
            position: relative
            display: flex
            flex-direction: column

            img
                width: 100%

            .image-details
                display: none
                flex-direction: column
                align-items: flex-start
                justify-content: space-between
                position: absolute
                height: 100%
                width: 100%
                background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 50%, rgba(0,0,0,1) 100%)

                .image-details-upper
                    padding: 10px 0px 10px 0px
                    width: 100%

                    .source-container
                        display: flex
                        justify-content: space-between
                        align-items: center
                        padding: 0px 5px

                        p
                            font-size: 0.8em
                            color: white
                            margin: 0 5px 0 5px

                        .twitter-icon
                            width: 25px
                            border-radius: 50%
                            margin: 0 5px 0 5px

                .image-details-lower
                    padding: 5px

                    .tag-container
                        display: flex
                        justify-content: flex-start
                        padding: 5px 5px 5px 5px

                        .tag-name
                            height: 1em
                            font-size: 0.8em
                            font-weight: bold
                            color: white
                            padding: 2px 5px 2px 5px
                            margin: 2px 2px 2px 2px
                            background-color: #5bcc0f
                            border-radius: 3px

            &:hover
                .image-details
                    display: flex
